<template>

<div class="itunes">
    <v-layout row>
        <v-flex  head-room >
            <!-- <div class="itunes"></div> -->
        </v-flex>
    </v-layout>
    <h1>MUSIC + SELECTOR</h1>
    <v-layout row>
        <v-flex xs4>
        </v-flex>
        <v-flex >
            <form @submit.prevent="getMusicByArtist()" class="tbox">
                <v-text-field fab dark color="white" v-model="artist" name="input-3" label="Search by Artist..." value="Input text"></v-text-field>
            </form>
        </v-flex>
    </v-layout>
    </div>
</template>

<script>
    export default {
        name: 'itunes',
        data() {
            return {
                artist: ''
            }
        },
        methods: {

            // Search By Artist
            getMusicByArtist() {
                this.$store.dispatch('getMusicByArtist', this.artist)
            },

            // Adds to MyTunes DB
            addToMyTunes(result) {
                var single = {
                    title: result.trackName,
                    artist: result.artistName,
                    albumArt: result.artworkUrl100,
                    price: result.trackPrice,
                    preview: result.previewUrl
                }
                this.$store.dispatch('addToMyTunes', single)
            },

            // Recovers Saved Songs
            getMyTunes() {
                this.$store.dispatch('getMyTunes')
            }
        },
        computed: {
            results() {
                return this.$store.state.results
            }
        }
    }
</script>
<style>
    .head-room {
        height: 200px
    }

    .test {
        max-width: 100%;
        background-color: red
    }

    .itunes {
        background-image: url("../universepurp.jpg");
        width: auto;
        overflow: hidden;
        
    }

    .leg-room {
        height: 100px
    }

    .tbox {
        width: 350px;
        color: white
    }
</style>


